import W from 'wistia_namespace.js';
import '../embeds/media/E-v1/_integrations.coffee';
import '../embeds/media/E-v1/_ga.coffee';
import '../embeds/media/E-v1/_hubspot.coffee';
import '../embeds/media/E-v1/_marketo.coffee';
import { registerPlugin } from '../embeds/media/E-v1/_hubspot_v2.js';

export const runIntegrationsSetup = () => {
  registerPlugin();

  W.integrations.setup();
  W.bind('ev1initend', () => {
    W._initializers.initIntegrations();
  });
  W.bind('ev1destroystart', () => {
    W._destructors.destroyIntegrations();
  });
};
